import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	onLoad() {
		let _this = this;
		uni.login({
			provider: 'weixin',
			success: function(info) {
				console.log(info)
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						console.log(infoRes)
						console.log('用户昵称为：' + infoRes.userInfo.nickName);
						console.log('用户昵称为：' + infoRes.userInfo.avatarUrl);
						_this.userImg = infoRes.userInfo.avatarUrl;
						uni.request({
							method:'GET',
							url:`https://api.weixin.qq.com/sns/jscode2session?appid=wxb160d9138f3c51b7&secret=166ecf8012ec8b6bbbd0c91c5aae03e5&js_code=${info.code}&grant_type=authorization_code`,
							success: (e) => {
								console.log(e)
							},
							fail: (err) => {
								console.log(err)
							}
						})
					}
				});
			},
			fail: (err) => {
				console.log(err)
			}
		})
	},
	data() {
		return {
			userImg: '',
			list: [{
					name: '宝宝信息',
					url: 'userBabyInfo/userBabyInfo',
					imgUrl: ''
				},
				{
					name: '我的信息',
					url: 'myInformation/myInformation',
					imgUrl: ''
				},
				{
					name: '我的咨询',
					url: 'myAdvice/myAdvice',
					imgUrl: ''
				},
				{
					name: '自查记录',
					url: 'inspectionRecords/inspectionRecords',
					imgUrl: ''
				},
				{
					name: '意见反馈',
					url: 'feedback/feedback',
					imgUrl: ''
				},
			]
		}
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			// this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			this.$store.state.forcedLogin = true;
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		toUserBabyInfo(e) {
			uni.navigateTo({
				url: e
			})
		}
	}
}
